mat-toolbar.mat-toolbar {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 10;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.3);
}
mat-toolbar.mat-toolbar mat-toolbar-row {
  padding: 0 16px 0 0;
}
mat-toolbar.mat-toolbar mat-icon {
  color: #FFFFFF;
}

app-root.page-home mat-toolbar.mat-toolbar {
  background-color: #1976D2;
}
@media (min-width: 481px) {
  app-root.page-home mat-toolbar.mat-toolbar:not(.transitioning) {
    background-color: transparent;
    transition: background-color 0.2s linear;
  }
}

app-root.page-home mat-toolbar.mat-toolbar,
app-root.page-features mat-toolbar.mat-toolbar,
app-root.page-events mat-toolbar.mat-toolbar,
app-root.page-resources mat-toolbar.mat-toolbar {
  box-shadow: none;
}
@media (min-width: 481px) {
  app-root.page-home mat-toolbar.mat-toolbar,
app-root.page-features mat-toolbar.mat-toolbar,
app-root.page-events mat-toolbar.mat-toolbar,
app-root.page-resources mat-toolbar.mat-toolbar {
    position: absolute;
  }
}

@media (min-width: 1280px) {
  app-root.folder-api mat-toolbar.mat-toolbar .hamburger.mat-button,
app-root.folder-cli mat-toolbar.mat-toolbar .hamburger.mat-button,
app-root.folder-docs mat-toolbar.mat-toolbar .hamburger.mat-button,
app-root.folder-guide mat-toolbar.mat-toolbar .hamburger.mat-button,
app-root.folder-start mat-toolbar.mat-toolbar .hamburger.mat-button,
app-root.folder-tutorial mat-toolbar.mat-toolbar .hamburger.mat-button {
    margin: 0 8px 0 0;
  }
}

.hamburger.mat-button {
  height: 100%;
  margin: 0 8px 0 0;
  padding: 0;
}
@media (min-width: 1280px) {
  .hamburger.mat-button {
    margin: 0 16px 0 -64px;
  }
}
@media (max-width: 480px) {
  .hamburger.mat-button {
    min-width: 15%;
  }
}
.hamburger.mat-button:not(.starting) {
  transition-duration: 0.4s;
  transition-property: color, margin;
  transition-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1);
}
.hamburger.mat-button:hover {
  color: #FAFAFA;
}
.hamburger.mat-button .mat-icon {
  color: #FFFFFF;
  position: inherit;
}

.nav-link.home {
