app-nav-nodes {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 80%;
}
app-nav-nodes ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  list-style-position: inside;
  padding: 0px;
  margin: 0px;
}
app-nav-nodes ul li {
  padding-bottom: 2px;
  list-style-type: none;
  cursor: pointer;
}
app-nav-nodes ul li:focus {
  outline: none;
}
app-nav-nodes ul li a.nav-link {
  margin: 0 4px;
  padding: 0px;
  cursor: pointer;
}
app-nav-nodes ul li a.nav-link .nav-link-inner {
  border-radius: 4px;
  padding: 8px 16px;
}
app-nav-nodes ul li a.nav-link .nav-link-inner:hover {
