  background: rgba(255, 255, 255, 0.15);
  border-radius: 1px;
  box-shadow: 0 0 1px 2px rgba(255, 255, 255, 0.8);
}
app-nav-nodes ul li a.nav-link:active .nav-link-inner {
  background: rgba(255, 255, 255, 0.15);
}
app-nav-nodes ul li.selected a.nav-link .nav-link-inner {
