}

app-search-input.search-container {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  min-width: 150px;
  height: 100%;
  margin-right: 16px;
}
app-search-input.search-container input {
  color: #444444;
  border: none;
  border-radius: 100px;
  background-color: #FFFFFF;
  padding: 5px 16px;
  margin-left: 8px;
  width: 180px;
  max-width: 240px;
  height: 50%;
  -webkit-appearance: none;
}
app-search-input.search-container input:hover {
  outline: none;
}
app-search-input.search-container input:-ms-input-placeholder {
  font-size: 14px;
